function test(){
  interface IProxyInfo{
    proxy:any
    msg:any
    target:any
  }
  let count = 0
  const proxyMap = new Map<any, any>()
  function createProxy(target:any, msg:any){
    const proxy = new Proxy(target, {
      get(target, prop, receiver) {
        console.log('get', prop)
        return Reflect.get(target, prop, receiver)
      },
      set(target, prop, value, receiver) {
        console.log('set', prop, value)
        return Reflect.set(target, prop, value, receiver)
      }
    })
    proxyMap.set(proxy, {proxy, msg, target})
    return proxy
  }
  const a = {name:'a', age:10, b:createProxy([createProxy({name:'b', age:20}, ++count)], ++count)}
  const p1 = createProxy(a, ++count)
  const p2 = createProxy(p1, ++count)
  const p3 = createProxy(p2, ++count)
  const p4 = createProxy(p3, ++count)
  const p5 = createProxy(p4, ++count)
  const p6 = createProxy(p5, ++count)
  const p7 = createProxy(p6, ++count)
  p7.name = 'p7'
  const c = _.cloneDeep(p7)
  console.log('ccccccccccccccccccc', c)
}
