    import store, { StoreType } from 'store2';
export interface IStoreUpdateEvent{
    key:string;
    value:any;
    store:StoreType;
}

interface IStores{
    read(key:string, defaultValue?:any):any;
    write(key:string, value:any, override?:boolean):void;
    ns:Record<string, any>
}
interface IStoresProxy extends IStores {
    clearAll():void;
    remove(key:string):void;
}

let is:IStores|undefined = undefined;
function setStoresProxy(s:IStores){
    is = s;
}
function getStoresNamespace(ns:string){
    if(!is)return null;
    let s = is.ns[ns];
    if(s)return s;
    s = is.read(ns);
    if(s)return is.ns[ns] = s;
    return is.ns[ns] = {}
}
function namespace(ns:string){
    function read<T>(key:string, defaultValue?:T){
        const proxy = getStoresNamespace(ns)
        if(proxy){
            return key in proxy ? proxy[key] : defaultValue
        }else{
            return store.namespace(ns).get(key, defaultValue)
        }
    }
    function save(key:string, value:any, override?:boolean){
        const proxy = getStoresNamespace(ns);
        if(proxy){
            proxy[key] = value
            is?.write(ns, proxy);
        }else{
            store.namespace(ns).set(key, value, override);
        }
    }
    function clearAll(){
        if(is){
            is.write(ns, '')
        }else{
            store.namespace(ns).clearAll();
        }
    }
    function remove(key:string){
        const proxy = getStoresNamespace(ns);
        if(proxy && key in proxy){
            delete proxy[key]
            is?.write(ns, proxy);
        }else{
            store.namespace(ns).remove(key)
        }
    }
    return {
        read, save, clearAll, remove
    }
}
export const storeUtils = {
    namespace,
    setStoresProxy
}