import { appController } from "@app/model";
import { Dispatcher, indexedDbUtils, teamGroupMap } from "@fanfanlo";
import { env } from "@src/confs/env";

export class MainController extends Dispatcher{
    async init(){
        await this.start();
    }
    async start(){
        teamGroupMap.def.gateway = env.gateway;
        await indexedDbUtils.init()
        await appController.init()
        
    }
}

export const mainController = new MainController()