
import { isConsulePrintable } from '../utils/lodash';
import _ from 'lodash';
import { format } from "date-fns";
let logIndex = 0;
let colorsIndex = 0;
const colors = [
  '#f1581f',
  '#003366',
  '#990033',
  '#663366',
  '#CCCC99',
  '#CC9999',
  '#003300',
  '#8cc540',
  '#009f5d',
  '#019fa0',
  '#019fde',
  '#ff1244',
  '#ff8345',
];
export class Log {
  static isInApp = typeof navigator !== 'undefined' && navigator.userAgent.indexOf('droid-lego-android----------') > -1;
  static eventWarnAdd = 'warnAdd';
  static eventErrorAdd = 'errorAdd';
  // static dispatcher = new EventDispatcher();
  static warnList = [];
  static errorList = [];
  static logStoreMaxLength = 100;
  static addWarn = (s:string) => {
    Log.addLog(Log.warnList, s, Log.eventWarnAdd);
  };
  static addError = (s:string) => {
    Log.addLog(Log.errorList, s, Log.eventErrorAdd);
  };
  static addLog = (list:string[], s:string, event:any) => {
    if (list.length > Log.logStoreMaxLength) {
      list.shift();
    }
    // s = `${formatDate(new Date(), 'DD HH:MM:SS')}`;
    s = format(new Date(), "dd hh:mm:ss");
    // s = `${new Date().toLocaleString()}`
    list.push(s);
    // Log.dispatcher.dispatchEvent(new DataEvent(event, list));
  };
  static forbidden = [];
  static allowed = [];
  prefixies:string[]
  prefix:string
  isPaused:boolean
  globalIndex:number
  index:number
  parent?:Log|undefined
  isChildrenPaused:boolean
  isForcePrint:boolean
  color:string
  logger:Log
  constructor(paused:boolean, ...prefixies:string[]) {
    this.prefixies = prefixies;
    this.prefix = prefixies.join(' ');
    this.isPaused = !!paused;
    this.globalIndex = logIndex++;
    this.index = 0;
    this.parent = undefined;
    this.isChildrenPaused = false;
    this.isForcePrint = false;
    this.color = colors[colorsIndex++ % colors.length];
    this.logger = this;
  }
  static countMap = new Map<string, number>();
  static createCountedLogger = (paused:boolean, name:string, ...prefixies:string[]) => {
    let v = Log.countMap.get(name) ?? 0;
    v++;
    Log.countMap.set(name, v)
    let prefix = `${name} ${v}`;
    let arr = prefixies.concat(prefix);
    let log = new Log(paused, ...arr);
    log.index = v;
    return log;
  };
  sub = (paused:boolean, name:string) => {
    let log = Log.createCountedLogger(paused, `${name}`, ...this.prefixies);
    log.parent = this;
    return log;
  };
  set forcePrint(p) {
    this.isForcePrint = !!p;
  }
  get forcePrint() {
    return this.isForcePrint;
  }
  set pause(p) {
    this.isPaused = !!p;
  }
  get pause() {
    return this.isPaused;
  }
  set childrenPaused(p) {
    this.isChildrenPaused = !!p;
  }
  get childrenPaused() {
    return this.isChildrenPaused;
  }
  get canPrint() {
    if (this.isForcePrint) return true;
    if (this.isPaused) return false;
    let p = this.parent;
    while (p) {
      if (p.isChildrenPaused) return false;
      p = p.parent;
    }
    return true;
  }
  warn = (...args:unknown[]) => {
    const arr = this.print.apply(this, args)?? [];
    Log.addWarn(arr.join(' '));
  };
  error = (...args:unknown[]) => {
    const isForcePrint = this.isForcePrint;
    this.isForcePrint = true;
    const arr = this.print.apply(this, args) ??[];
    this.isForcePrint = isForcePrint;
    Log.addError(arr.join(' '));
  };
  print = (...args:unknown[]) => {
    if (!this.canPrint) return;
    let l = this;
    let retArray:any[] = [];
    let arr:any[] = [];
    let format = '';
    format = this.loopParent(l, arr, (l:Log) => l.globalIndex, format);

    arr.unshift('@');
    if (!Log.isInApp) {
      format += '%c%s ';
      arr.unshift('color:#000000;font-weight: bold;');
    }

    format = this.loopParent(l, arr, (l:Log) => l.index, format);

    if (!Log.isInApp) {
      format += '%c%s ';
      arr.push('color:#000000;font-weight: bold;');
    }
    if (Log.isInApp) {
      for (let i = 0; i < args.length; i++) {
        if (!isConsulePrintable(args[i] as {
          nodeType:number
          nodeName:string
      })) {
          try {
            const o = args[i];
            let v = '';
            if (_.isError(o)) {
              v = o.message + '\n' + o.stack;
            } else {
              v = JSON.stringify(args[i]);
              // chrome扩展不允许eval所以这里做一下临时处理
              // v = JSON.stringify(jc.decycle(args[i]));
            }
            args[i] = v;
          } catch (e) {
            console.log('log print json stringify error', e);
            console.log('log print json stringify error stack is', _.isError(e) ? e.stack : '');
          }
        }
      }
    }
    arr.push(this.prefix);
    arr = arr.concat(args);
    console.log(format, ...arr);
    retArray = arr.slice()
    return retArray;
  };
  log = this.print.bind(this);
  loopParent = (l:Log|undefined, arr:any[], fn:(l:Log)=>number, format:string) => {
    while (l) {
      arr.unshift(fn(l));
      if (!Log.isInApp) {
        format += '%c%s ';
        arr.unshift(`color:${l.color};font-weight: bold;`);
      }
      l = l.parent;
    }
    return format;
  };
}

