export interface ILogExtraConfigExtraMatch {
  prefix?: string;
  fn?: (from: string, target: any, ...args: any[]) => boolean;
}
export interface ILogExtraConfig {
  printName?: string;
  alias?: string;
  extraMatch?: ILogExtraConfigExtraMatch;
}

export function checkCanPrintLog(conf: ILogExtraConfig, from: string, target: any, ...args: any[]) {
  if (!conf) return true;
  if (!conf.extraMatch) {
    if (conf.alias) return from == conf.alias;
    return true;
  }
  if (conf.extraMatch.prefix && from.indexOf(conf.extraMatch.prefix) == -1) return false;
  if (conf.extraMatch.fn && !conf.extraMatch.fn(from, target, ...args)) return false;
  if (conf.extraMatch.fn) return conf.extraMatch.fn(from, target, ...args);
  if (conf.extraMatch.prefix) return from == conf.extraMatch.prefix;
  return false;
}

const logMap = new WeakMap<any, ILogExtraConfig>();
/**
 * 添加打印目标，这个打印可用于一些公用打印，比如ProxyWatch里要打印特定target的信息。
 * @param target 要打印的target
 * @param conf 打印配置。如果配置为空则打印，如果配置了alias则比较alias与输入的frome相同则打印，如果配置了extraMatch则比较extraMatch，首先检测extrMatch.fn，如果fn存在则调用fn，fn返回true则打印。如果fn不存在则比较prefix，如果prefix存在则比较prefix与输入的from是否相同，如果相同则打印。
 */
export function logAddPrintTarget(target: any, conf: ILogExtraConfig = {}) {
  // console.log(
  //   'logAddPrintTarget target=',
  //   target,
  //   'conf=',
  //   conf,
  //   'objectCount=',
  //   objectCountUtils.getObjectCount(target),
  // );
  logMap.set(target, conf);
}
export function logRemovePrintTarget(target: any) {
  logMap.delete(target);
}

export function logGetPrintTargetConf(target: any) {
  return logMap.get(target);
}
export function checkTargetCanPrintLog(target: any, from: string, ...args: any[]) {
  const conf = logMap.get(target);
  // console.log('checkTargetCanPrintLog logMap.get(target)=', conf, target);
  if (!conf) {
    // console.warn(
    //   '11111 logmap not found target=',
    //   'objectCount=',
    //   objectCountUtils.getObjectCount(target),
    //   'target=',
    //   target,
    // );
    // for (const key in logMap) {
    //   console.warn('logMap key=', key, 'logMap value=', logMap.get(key));
    // }
    return { conf, ok: false };
  }
  const ok = checkCanPrintLog(conf, from, target, ...args);
  return { conf, ok };
}

export function printPassedTargetLog(log: Log, target: any, from: string, ...args: any[]) {
  const conf = logMap.get(target);
  if (!conf) return;
  if (!checkCanPrintLog(conf, from, target, ...args)) return;
  const list: any[] = [];
  if (conf.printName) list.push(conf.printName);
  else {
    list.push(from);
    if (conf.alias) list.push('alias=', conf.alias);
    if (conf.extraMatch?.prefix) list.push('prefix=', conf.extraMatch.prefix);
  }
  list.push(...args);
  log.print(...list);
}
